<template>
  <!--选项-->
  <div class="music-btn">
    <input id="file" ref="file" class="input-file" type="file" name="file" accept=".csv" @change="uploadFile" />
    <label for="file">添加歌曲</label>
    <router-link to="/music/playlist" tag="span">正在播放</router-link>
    <router-link to="/music/historylist" tag="span">我听过的</router-link>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import Papa from 'papaparse' // 解析cvs插件 市面上使用较多的
import jschardet from 'jschardet' // 编码识别

export default {
  methods: {
    // 检查编排
    checkEncoding(base64Str) {
      // 这种方式得到的是一种二进制串
      const str = atob(base64Str.split(';base64,')[1]) // atob  方法 Window 对象 定义和用法 atob() 方法用于解码使用 base-64 编码的字符
      // 要用二进制格式
      let encoding = jschardet.detect(str)
      encoding = encoding.encoding
      // 有时候会识别错误
      if (encoding === 'windows-1252') {
        encoding = 'ANSI'
      }
      return encoding
    },
    uploadFile() {
      let reads = new FileReader()
      const file = document.getElementById('file').files[0]
      const _this = this;
      reads.readAsDataURL(file, 'utf-8')
      reads.onload = function(e) {
        // const data = e.target.result.replace(/\r\n/g, '\n').split('\n').filter(a => a).map(item => item.split(',').filter(a => a))
        // console.log(data)
        const data = e.target.result
        const encoding = _this.checkEncoding(data)

        Papa.parse(file, {
          encoding: encoding,
          complete: function(results) {
            const res = results.data
            if (res[res.length - 1] === '') { // 去除最后的空行 有些解析数据尾部会多出空格
              res.pop()
            }

            const data = res.filter(item => item.some(content => content)).map(item => item.map(content => content.replace(/\n/g, '').replace(/\r\n/g, '')));
            data.shift()

            _this.setPlaylist({
              list: data.map((item) => ({ name: item[0], url: item[1], id: item[1] }))
            })
          }
        })
      }
    },
    ...mapActions(['setPlaylist'])
  }
}
</script>

<style lang="less" scoped>
.input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.music-btn {
  width: 100%;
  height: 60px;
  font-size: 0;
  white-space: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  label, span {
    display: inline-block;
    height: 40px;
    box-sizing: border-box;
    margin-right: 8px;
    padding: 0 23px;
    border: 1px solid @btn_color;
    color: @btn_color;
    border-radius: 2px;
    font-size: 14px;
    line-height: 40px;
    overflow: hidden;
    cursor: pointer;
    &:hover,
    &.active {
      border-color: @btn_color_active;
      color: @btn_color_active;
    }
  }
  span {
    &:nth-last-of-type(1) {
      margin: 0;
    }
  }
  @media (min-width: 960px) {
    span.show-960,
    label.show-960 {
      display: none;
    }
  }
  @media (max-width: 960px) {
    span.show-960
    label.show-960 {
      display: inline-block;
    }
  }
  @media (max-width: 768px) {
    height: 50px;
    span, label {
      height: 35px;
      padding: 0 10px;
      margin-right: 6px;
      line-height: 35px;
    }
  }
}
</style>
